import React from "react";

export default function SlideThree(props) {
  return (
    <div className="section section--h-full">
      <div className="carousel--item-one">
        <img src="./images/jpg/MonkPhoto.jpg" alt="" />
      </div>
      <div className="section carousel--item-two">
        <h3 className="title">Monk Stuff</h3>
        <p className="body">
          After some very interesting life expierences, I started to learn
          everything that I could about everything. This drove me into rather
          interesting topics and by chance, I met an Archbishop. After two years
          of self study, my life directed me into a position where I moved in
          with the Archbishop to study Hesychasm. After a number of
          unexplainable occurances, I felt the want to come back to the world.
        </p>
      </div>
    </div>
  );
}
